.App {
  font-family: sans-serif;
  text-align: center;
}

.book {
  list-style: none;
  display: flex;
  text-align: left;
  margin: 1.5em 0;
  padding: 1em 0 2em 0;
  border-bottom: 1px solid #ccc;
}

.book img {
  min-width: 130px;
  max-width: 130px;
  min-height: 170px;
  max-height: 170px;
  overflow: hidden;
}

.book-info {
  padding-left: 2em;
}

.book-info p {
  font-size: 12px;
}
